import React from 'react';  // 'react' dependency is listed in package.json
import PropTypes from 'prop-types';
import AddFishForm from './AddFishForm';
import base from '../base';

class Inventory extends React.Component {
  state = {
    uid: null,
    owner: null
  };

  // lifecycle hook to handle case of refreshing the page; don't want to re-authenticate
  // every time.
  componentDidMount() {
    base.onAuth((user) => {
      if (user) {
        this.authHandler(null, {user});
      }
    })
  }

  handleChange = (e,key) => {
    const fish = this.props.fishes[key];

    // Copy the fish and update it with the new data.
    // Take a "spread" of fish and overlay the new attributes on top of it.
    const updatedFish = {
      ...fish,  // "..." is the "spread" operator.
      [e.target.name]: e.target.value  // Use square brackets to set a 'computed property'
    };

    // Now we need to pass the updatedFish to the App's global state object for syncing.
    this.props.updateFish(key, updatedFish);
  };

  renderLogin = () => {
    return (
     <nav className="login">
       <h2>Inventory</h2>
       <p>Sign in to manage your store's inventory</p>
       <button className="github" onClick={() => this.authenticate('github')}>Sign in with github</button>
       <button className="facebook" onClick={() => this.authenticate('facebook')}>Sign in with facebook</button>
       <button className="twitter" onClick={() => this.authenticate('twitter')}>Sign in with twitter</button>
     </nav>
    )
  };

  authenticate = (provider) => {
    console.log(`Trying to login with ${provider}`);
    base.authWithOAuthPopup(provider, this.authHandler);
  };

  logout = () => {
    base.unauth(); // Sever any ties with Firebase.
    this.setState({uid: null});
  };

  authHandler = (err, authData) => {
    console.log(authData);
    if (err) {
      console.error(err);
      return;
    }

    // Grab the store's info and update the state.
    // Connect directly with the Firebase DB, which allows us to use any of the Firebase APIs.
    const storeRef = base.database().ref(this.props.storeId);  // ref(), because we don't want the whole DB
    storeRef.once('value', (snapshot) => {
      const data = snapshot.val() || {};
      if (!data.ower) {
        storeRef.set({
          owner: authData.user.uid
        });
      }
      this.setState({
        uid: authData.user.uid,
        owner: data.owner || authData.user.uid
      });
    });
  };

  // Because this function is not render(), it's not automatically bound to its
  // containing class.  Hence, a bind() is needed in the class's constructor.
  renderInventory = (key) => {
    const fish = this.props.fishes[key];
    return (
      <div className="fish-edit" key={key}>
        <input type="text" name="name" value={fish.name} placeholder="Fish name" onChange={(e) => this.handleChange(e, key)}/>
        <input type="text" name="price" value={fish.price} placeholder="Fish price" onChange={(e) => this.handleChange(e, key)}/>
        <select type="text" name="status" value={fish.status} placeholder="Fish status" onChange={(e) => this.handleChange(e, key)} >
          <option value="available">Fresh!</option>
          <option value="unavailable">Sold Out!</option>
        </select>
        <textarea type="text" name="desc" value={fish.desc} placeholder="Fish Desc" onChange={(e) => this.handleChange(e, key)}/>
        <input type="text" name="image" value={fish.image} placeholder="Fish Image" onChange={(e) => this.handleChange(e, key)}/>
        <button onClick={() => this.props.removeFish(key)} >Remove Fish</button>
    </div>
    )
  };

  render() {
    // If logout() had args, it would be said like so: onClick={(arg) => this.logout(arg)}
    const logout = <button onClick={this.logout}>Log out!</button>
    // Check if not logged in
    if (!this.state.uid) {
      return <div>{this.renderLogin()}</div>
    }

    if (this.state.uid!==this.state.owner) {
      return <div><p>Sorry, you aren't the owner of this store!</p>{logout}</div>
    }
    return (
     <div>
       <h2>Inventory</h2>
       {logout}
       {Object.keys(this.props.fishes).map(this.renderInventory)}
       <AddFishForm addFish={this.props.addFish}/>
       <button onClick={this.props.loadSamples}>Load Sample Fishes</button>
     </div>
    )
  }

  // 'static', because we don't need a copy for every single instance of Inventory.
  static propTypes = {
    loadSamples: PropTypes.func.isRequired,
    addFish: PropTypes.func.isRequired,
    removeFish: PropTypes.func.isRequired,
    updateFish: PropTypes.func.isRequired,
    fishes: PropTypes.object.isRequired,
    storeId: PropTypes.string.isRequired
  };

}

export default Inventory;



