import React from 'react';
import PropTypes from 'prop-types';
import {formatPrice} from "../helpers";

class Fish extends React.Component {
  render() {
    const {details, k} = this.props;  // "details" is only a Fish property, not in global state
    // The above is the ES6 way of saying...
    //const details = this.props.details;
    //const k = this.props.k;
    const isAvail = details.status === 'available';
    const btnTxt = isAvail ? 'Add To Order' : 'SOLD OUT!';
    return (
     <li className="menu-fish">
       <img src={details.image} alt={details.name}/>
       <h3 className="fish-name">{details.name}</h3>
       <span className="price">{formatPrice(details.price)}</span>
       <p>{details.desc}</p>
       <button onClick={() => this.props.addToOrder(k)} disabled={!isAvail}>{btnTxt}</button>
     </li>
    )
  }

  static propTypes =  {
    addToOrder: PropTypes.func.isRequired,
    k: PropTypes.string.isRequired,
    details: PropTypes.object.isRequired
  };
}


export default Fish;


