import React from 'react';  // 'react' dependency is listed in package.json
import PropTypes from 'prop-types';
import OrderEntry from './OrderEntry';
import {formatPrice} from "../helpers";
import CSSTransitionGroup from 'react-addons-css-transition-group';

class Order extends React.Component {
  orderTotal(order, fishes) {
    return (
     Object.keys(order)
     .map(key => order[key] * (fishes[key] ? fishes[key].price : 0)).reduce(function (a, b) {
       return a + b;
     }, 0)
    )
  }

  render() {
    return (
     <div className="order-wrap">
       <h2>Your Order</h2>
       <CSSTransitionGroup className="order" component="ul" transitionName="order"
         transitionEnterTimeout={500}
         transitionLeaveTimeout={500}
       >
         {
           Object.keys(this.props.order).map (key => {
             var fish = this.props.fishes[key];
             var price = fish ? fish.price : undefined;
             var name = fish ? fish.name : undefined;
             return <OrderEntry
              key={key}
              k={key}
              price={price}
              amt={this.props.order[key]}
              name={name}
              removeFromOrder={this.props.removeFromOrder}
             />
           })
           }
         <li className="total">Total: <span
          className="price">{formatPrice(this.orderTotal(this.props.order, this.props.fishes))}</span>
         </li>
       </CSSTransitionGroup>
     </div>
    )
  }

  static propTypes = {
    order: PropTypes.object.isRequired,
    removeFromOrder: PropTypes.func.isRequired,
    fishes: PropTypes.object.isRequired
  }
}

export default Order;



