import React from 'react';  // 'react' dependency is listed in package.json
import PropTypes from 'prop-types';

class AddFishForm extends React.Component {
  render() {
    return (
     // The ref on the form allows clearing out the form on submit.
     <form ref={(input) => this.fishForm = input} className="fish-edit" onSubmit={(e) => this.createFish(e)}>
       <input ref={(input) => this.name = input} type="text" placeholder="Fish Name"/>
       <input ref={(input) => this.price = input} type="text" placeholder="Fish Price"/>
       <select ref={(input) => this.status = input}>
         <option value="available">Fresh!</option>
         <option value="unavailable">Sold Out!</option>
       </select>
       <textarea ref={(input) => this.desc = input} placeholder="Fish Desc"/>
       <input ref={(input) => this.image = input} type="text" placeholder="Fish Image"/>
       <button type="submit">+ Add Item</button>
     </form>
    )
  }

  createFish(event) {
    event.preventDefault();
    console.log('Going to make some fish! 🐠');
    const fish = {
      name: this.name.value,
      price: this.price.value,
      status: this.status.value,
      desc: this.desc.value,
      image: this.image.value,
    };
    // console.log(fish);
    this.props.addFish(fish);
    this.fishForm.reset();  // Clear out the fish [???] a JS reset
  }

  static propTypes = {
    addFish: PropTypes.func.isRequired
  };
}

export default AddFishForm;



