import React from 'react';  // 'react' dependency is listed in package.json
import PropTypes from 'prop-types';
import Header from './Header';
import Order from './Order';
import Inventory from './Inventory';
import Fish from './Fish';
import sampleFishes from '../sample-fishes';
import base from '../base';

class App extends React.Component {

  // The new way to do getInitialState;
  state = {
    fishes: {},
    order: {}
  };
  // Above is how to add a property to a property *initializer*, which
  // means that every instance of the App class will get its own state.

  // Set up the state sync just before mounting the component
  componentWillMount() {
    // Hook state up to Firebase
    this.ref = base.syncState(`${this.props.params.storeId}/fishes`, {
      context: this,
      state: 'fishes'
    });

    // check if any order in localStorage.
    const locStorageRef = localStorage.getItem(`order-${this.props.params.storeId}`);
    if (locStorageRef) {
      // Update App component's state.
      this.setState({
        order: JSON.parse(locStorageRef)
      });
    }
  }


  // Unhooks from Firebase.
  componentWillUnmount() {
    base.removeBinding(this.ref);
  }

  componentWillUpdate(nextProps, nextState) {
    // console.log('Something changed');
    // console.log({nextProps, nextState}); // If passed with curlies, objects show with names in inspector

    // NOTE: The component that will update is Order, not App.  So at update time,
    // "this.props.params" will refer to the Order's params, not the App's.  Therefore,
    // must pass the appropriate params down
    // from App to Order at render time (see the App's render() func).
    localStorage.setItem(`order-${this.props.params.storeId}`, JSON.stringify(nextState.order));
  }

  addFish = (fish) => {
    // update our state
    // Don't do this, although it would seem to be the most straightforward:
    // this.state.fishes.fish1 = fish;

    // Make a copy of state.  "..." is a "spread", which takes everything in your
    // existing object and "spreads" it into the new object [a "deep-copy"?]
    const fishes = {...this.state.fishes};

    // Add in the new fish
    const ts = Date.now();
    fishes[`fish-${ts}`] = fish;

    // set state; tell React which state I would like to update,\
    // so that React doesn't have to watch your entire huge state object.
    this.setState({fishes: fishes});
    // Or, you can say it like so:
    // this.setState({fishes});

  };

  updateFish = (key, updatedFish) => {
    const fishes = {...this.state.fishes};
    fishes[key] = updatedFish;
    this.setState({fishes});
  };

  removeFish = (key) => {
    const fishes = {...this.state.fishes};
    // delete fishes[key] doesn't work with Firebase!
    fishes[key] = null;
    this.setState({fishes});
  };

  addToOrder = (key) => {
    const order = {...this.state.order};
    order[key] = order[key] + 1 || 1;
    this.setState({order: order});
  };

  removeFromOrder = (key) => {
    const order = {...this.state.order};
    if (order[key] !== undefined) {
      if (order[key] > 1) {
        order[key] -= 1;
      } else {
        delete order[key]; // Can use 'delete' here, since Order is not kept in Firebase.
      }
    }
    this.setState({order});
  };

  // Need to make loadSamples() available to the components that need to call it;
  // which is done via (a) binding loadSamples() to App's "this", and (b) passing it
  // to the interested components via properties that are set at render time.
  loadSamples = () => {
    this.setState({
      fishes: sampleFishes
    })
  };
  // The above version of loadSamples uses an arrow function to accomplish the correct binding
  // of 'this':  instead of binding to the local function scope as in the case of a function ("New function,
  // new scope"), 'this' in the context of an arrow function binds to the parent scope, which is
  // the App class, which is what we want.  Same for removeFish(), etc.
  // NOTE:  This works because we're using Babel to compile down to Javascript; it's not yet native.

  // Every React class needs a render.
  render() { // Can only return one parent element
    return (
     <div className="catch-of-the-day">
       <div className="menu">
         <Header tagline="Fresh Seafood Market"/>
         <ul className="list-of-fishes">
           {
             Object.keys(this.state.fishes)
             .map(key => <Fish
                key={key}
                // React needs and uses 'key' prop, but doesn't pass it down (that
                // is, it does not expose the 'key' prop for our use;
                // So, if the component also needs it, we have to use another name.
                k={key}
                details={this.state.fishes[key]}
                addToOrder={this.addToOrder}
             />)
           }
         </ul>
       </div>
       <Order
        fishes={this.state.fishes}
        order={this.state.order}
        params={this.props.params}
        removeFromOrder={this.removeFromOrder}
       />
       <Inventory
        addFish={this.addFish}
        updateFish={this.updateFish}
        removeFish={this.removeFish}
        loadSamples={this.loadSamples}
        fishes={this.state.fishes}
        storeId={this.props.params.storeId}
       />
     </div>
    )
  }

  static propTypes = {
    params: PropTypes.object.isRequired
  };
}

export default App;


