import React from 'react';
import PropTypes from 'prop-types';
import {formatPrice} from "../helpers";
import CSSTransitionGroup from 'react-addons-css-transition-group';

class OrderEntry extends React.Component {
  render() {
    // Cool, can store JSX in a var or const!
    const btnRemove = <button onClick={() => this.props.removeFromOrder(this.props.k)}>X</button>;
    return (
     <li key={this.props.k}>
       <span>
         { /* React will delete the old element after the timeout period */ }
         <CSSTransitionGroup
           component="span"
           className="count"
           transitionName="count"
           transitionEnterTimeout={250}
           transitionLeaveTimeout={250}
         >
         <span className="amount" key={this.props.amt}>
           {this.props.amt}
         </span>
         </CSSTransitionGroup>
         lb {this.props.name} {btnRemove}
       </span>
       <span className="price">{formatPrice(this.props.price * this.props.amt)}</span>
     </li>
    )
  }

  static propTypes = {
    removeFromOrder: PropTypes.func.isRequired,
    k: PropTypes.string.isRequired,
    amt: PropTypes.number.isRequired
    // For some reason, prop-types validation doesn't recognize these when passed to OrderEntry...
    //name: React.PropTypes.string.isRequired,
    //price: React.PropTypes.number.isRequired
  };
}

export default OrderEntry;


